.elementWrap {
  display: flex;
  flex-direction: column;
  width: 100%;
  font-size: 18px;

  .elementInput {
    font-size: 16px;
    margin: 8px 0px;
    box-shadow: 0px 0px 3px 0.3px rgb(133, 133, 133);
    border: none;
    border-radius: 5px;
    padding: 10px;
    outline: none;
    color: rgb(141, 141, 141);
    background-color: transparent;
  }
}
